.project-form {
  list-style: none;
  margin: 0 auto;
  padding: 1rem;
  width: 90%;
  max-width: 50rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  background: white;
}
.project-form-row-inputs{
   display: flex;
    justify-content: space-evenly;
}
.project-form-title{
    width: 70%;
    margin: 0 auto;
}

.project-table-sub-menu{
  margin: 0 auto;
  position: relative;
  cursor: pointer;
}
.project-table-sub-menu:hover .project-table-sub-icon-content{
   display: block;
}

.project-table-sub-icon-content{
    z-index: 2;
    border-radius: 10px;
    position: absolute;
    display: none;
    left: -70px;
}
.project-table-sub-icon-content a,
.project-table-sub-icon-content button{
  float: left;
    text-decoration: none;
    background-color: white;
    padding: 8px 20px;
    width: 100%;
    border: 1px solid rgba(0,0,0,.2);
    box-shadow: 0 1px 2px rgba(0,0,0,.2);
    color: black;
    text-align: center;
    font-size: 16px;
    cursor: pointer;
}
.project-table-sub-icon-content a:hover,
.project-table-sub-icon-content button:hover{
    background-color: #ebebeb;
}

.search-container{
    display: flex;
    justify-content: space-between;
    width: 90%;
    margin: auto;
    height: 200px;
}
.search-input-group{
    display: flex;
    align-items: center;
    margin-left: 20px;
    border: 1px solid black;
    width: 200px;
    height: 40px;
}
.search-input-group input{
    height: 100%;
    width: 100%;
    border: none;
    outline: none;
    background-color:#ddd;
    font-size: 20px;
    padding-left: 10px;
}
.search-input-container{
    display: flex;
}
.search-input-icon {
    background-color: #ddd;
    z-index: 2;
    padding: 0;
    margin: 0;
    border: none;
    outline: none;
    height: 100%;
    padding-left: 10px;
    padding-top: 5px;
}
.search-button-item{
  color: black;
}
.search-button-status{
    display: flex;
    align-items: center;
}
.search-input-button{
    height: 200px;
}
@media (min-width: 700px) {
    .search-input-group{
        width: 300px;
    }
    .search-container{
        display: flex;
        justify-content: space-between;
        width: 90%;
        margin: auto;
        height: 100px;
    }
}



.status-container{
    background-color: transparent;
    width: 10px;
    height: 10px;
}
.status-item{

    border-radius: 50%;
    margin: auto;
    align-items: center;
    width: 8px;
    height: 8px;
}
.red{
    background-color: #f34343;
}
.green{
    background-color: green;
}
.blue{
    background-color: blue;
}

.project-table-card-header{
    display: flex;
    justify-content: space-between;

    height:50px;
}
.project-table-container{
    width: 90%;
    margin : 0 auto;
    margin-bottom: 60px;
}
.project-table-main-status{
    display: flex;
    align-items: center;
    margin-right: 2px;
}
table {
    border-collapse: collapse;
    width: 100%;
}

th, td {
    padding: 8px;
    text-align: left;
    border-bottom: 1px solid #ddd;
}
.project-table-main{
    overflow:auto;
    margin-bottom: 20px;
}
.project-table-card-header-button{
    height: 200px;
}

.project_table__modal-content {
    padding: 0;
}

.project-table__modal-actions {
    text-align: right;
}


.card {
  margin: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  padding: 1rem;
  overflow: hidden;
  background: white;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.modal {
  z-index: 100;
  position: fixed;
  top: 22vh;
  left: 10%;
  width: 80%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 8px;
}

.modal__header {
  width: 100%;
  padding: 1rem 0.5rem;
  background: #2a006e;
  color: white;
}

.modal__header h2 {
  margin: 0.5rem;
}

.modal__content {
  padding: 1rem 0.5rem;
}

.modal__footer {
  padding: 1rem 0.5rem;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}

.modal-enter {
  transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.modal-exit {
  transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}

.authentication__header {
  color: white;
  text-align: center;
}

.authentication {
  width: 90%;
  max-width: 30rem;
  margin: 7rem auto;
  text-align: center;
}

.authentication form {
  margin-bottom: 1rem;
}
.authentication-form-header{
  display: flex;
  justify-content: space-between;
}

.authentication-conditions{
  padding: 10px;
}
.authentication-password{
  display: flex;
  justify-content: space-evenly;
}



